.pulse {
  position: absolute;
  width: 322px;
  height: 322px;
  text-align: center;
  border-radius: 50%; }

.pulse {
  background-color: #fdbacc;
  position: relative;
  transition: .5s;
  margin: 42px 0px 0px 32px; }

.pulse {
  background-color: #fdbacc; }

.pulse::before,
.pulse::after,
.pulse > .circle_dump {
  animation-delay: .2s;
  content: '';
  position: absolute;
  border: 5px solid #fdbacc;
  left: -30px;
  right: -30px;
  top: -30px;
  bottom: -40px;
  border-radius: 50%;
  animation: pulse 2s linear infinite; }

.pulse::after {
  animation-delay: .3s;
  left: -25px;
  right: -25px;
  top: -25px;
  bottom: -30px;
  border: 16px solid #fdbacc; }

.pulse > .circle_dump {
  left: -20px;
  right: -20px;
  top: -20px;
  bottom: -20px;
  border: 22px solid #fdbacc;
  z-index: -1;
  animation-delay: 1.2s; }

@keyframes pulse {
  0% {
    transform: scale(0.5);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    transform: scale(1.2);
    opacity: 0; } }

